<template>
  <section id="news">
   <h2>Новости</h2>
  </section>
</template>

<script>
  export default {
    name: 'SectionNews',
    provide: {
      theme: { isDark: true },
    },
    computed: {
      maxHeight () {
        const height = this.$vuetify.breakpoint.mdAndUp ? '30vh' : '20vh'
        return `calc(${height} - ${this.$vuetify.application.top}px)`
      },
    },
  }
</script>
